"use client";
import React, { useState } from 'react';
import { useRouter } from 'next/router';
import PostJob from '@/app/data/PostJob';

const PostJobsPage = () => {
    return (
        <div>
            <h1>Post a Job</h1>
            <PostJob />
            <p>This page will allow you to post new job listings.</p>
        </div>
    );
};

const [formData, setFormData] = React.useState({
    title: '',
    description: '',
    location: '',
    salary: '',
});

const [submitted, setSubmitted] = React.useState(false);

const handleChange = (e) => {
    setFormData({
        ...formData,
        [e.target.name]: e.target.value,
    });
};

const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitted(true);
    // Here you could send formData to your backend
};

return (
    <div className="post-jobs-container">
        <h1 className="post-jobs-title">Post a Job</h1>
        <p className="post-jobs-desc">Fill out the form below to post a new job listing.</p>
        <form className="post-jobs-form" onSubmit={handleSubmit}>
            <label>
                Job Title:
                <input
                    type="text"
                    name="title"
                    value={formData.title}
                    onChange={handleChange}
                    required
                />
            </label>
            <label>
                Description:
                <textarea
                    name="description"
                    value={formData.description}
                    onChange={handleChange}
                    required
                />
            </label>
            <label>
                Location:
                <input
                    type="text"
                    name="location"
                    value={formData.location}
                    onChange={handleChange}
                    required
                />
            </label>
            <label>
                Salary:
                <input
                    type="text"
                    name="salary"
                    value={formData.salary}
                    onChange={handleChange}
                />
            </label>
            <button type="submit" className="post-jobs-btn">Post Job</button>
        </form>
        {submitted && (
            <div className="post-jobs-success">
                <p>Job posted successfully!</p>
            </div>
        )}
    </div>
);

export default PostJobsPage;